$color-gray: #F0F0F0;
$color-orange: #E4C1AE;

%font-light{
    font-weight: 300;
}

%font-bold{
    font-weight: 700;
}

body{
    padding: 100px 0;
    background-color: $color-gray;
    font-size: 16px;
    font-family: 'Roboto', sans-serif;
    @extend %font-light;
    cursor: default;
}

.post_card{
    background-color: #fff;

    .picture{
        
        picture{
            
            img{
                width: 100%;
                height: 100%;
            }
        }
    }
    
    .content{

        .box{
            width: 100%;
            max-width: 350px;
            height: 407px;
            
            .title{
                margin-top: 24.57%;
                @extend %font-bold;
                font-size: 42px;
            }
        
            .text{
                margin-top: 12.28%;
            }

            .button{
                margin-top: 10%;
        
                button{
                    padding: 14px;
                    min-width: 135px;
                    background-color: $color-orange;
                    color: #fff;
                    border: none;
                    border-radius: 2em;
                    @extend %font-bold;
                    outline: none;
                    cursor: pointer;
                    line-height: 1.2em;
                }
            }
        }

    }

}